import "./App.css";
import React, { useEffect, useState } from "react";



export default function App() {
  const [Items, setItems] = useState([]);
  function getItems() {
    fetch("https://fastapi48.deta.dev/items/")
      .then((res) => res.json())
      .then((data) => {
        setItems(data);
      }
      );
  }

  function addItem(name, dsc) {
    fetch("https://fastapi48.deta.dev/items/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name: name,
        description: dsc,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        getItems();
      });
  }

  function deleteItem(id) {
    fetch(`https://fastapi48.deta.dev/items/${id}`, {
      method: "DELETE",
    })
      .then((res) => res.json())
      .then((data) => {
        getItems();
      });
  }

  useEffect(() => {
    getItems();
  }, []);

  return (
    <div className="">
      <h1 className="headCard">ATÖLYE MTT Envanter sistemi</h1>
      {Items &&
        Items.map((item) => (
          <div key={item.item_id} className='mycard'>
            <h2>{item.name}</h2>
            <p>{item.description}</p>
            <button 
            className="deleteButton"
            onClick={() => deleteItem(item.item_id)}>Sil</button>
          </div>
        ))}
      <div className="mycard">
        <h2>Yeni Ekipman Ekle</h2>
        <form
          onSubmit={(e) => {
            e.preventDefault();
            addItem(e.target.name.value, e.target.description.value);
          }}
        >
          <input type="text" name="name" placeholder="Ekipman Adı" />
          <input type="text" name="description" placeholder="Açıklama" />
          <button type="submit">Ekle</button>
        </form>
      </div>
    </div>
  )
}
